<template>
  <v-card>
    <v-card-title class="secondary white--text headline">
      Hazards Directory
    </v-card-title>
    <v-layout
      justify-space-between
      pa-3>
      <v-flex xs5>
        <v-treeview
          :active.sync="active"
          :items="items"
          :load-children="fetchhazards"
          :open.sync="open"
          activatable
          active-class="primary--text"
          class="grey lighten-5"
          open-on-click
          transition>
          <v-icon
            v-if="!item.children"
            slot="prepend"
            slot-scope="{ item, active }"
            :color="active ? 'primary' : ''"
          >mdi-account</v-icon>
        </v-treeview>
      </v-flex>
      <v-flex
        d-flex
        text-xs-center>
        <v-scroll-y-transition mode="out-in">
          <div
            v-if="!selected"
            class="title grey--text text--lighten-1 font-weight-light"
            style="align-self: center;">
            Select a hazard to view details
          </div>
          <v-card
            v-else
            :key="selected.id"
            class="pt-4 mx-auto"
            flat
            max-width="400">
            <v-card-text>
              <v-avatar size="120">
                <img :src="randomAvatar()" alt="avatar">
              </v-avatar>
              <h3 class="headline mb-2">
                {{ selected.name }}
              </h3>
              <div class="blue--text mb-2">{{ selected.creationDate }}</div>
              <div class="blue--text subheading font-weight-bold">{{ selected.employeeName }}</div>
            </v-card-text>
            <v-divider></v-divider>
            <v-layout
              tag="v-card-text"
              text-xs-left
              wrap>
              <v-flex tag="strong" xs5 text-xs-right mr-3 mb-2>Location:</v-flex>
              <v-flex><a :href="`//${selected.description}`" target="_blank">{{ selected.location.city }}</a></v-flex>
              <v-flex tag="strong" xs5 text-xs-right mr-3 mb-2>Description:</v-flex>
              <v-flex>
                {{ selected.description +' .'+ selected.description }}
              </v-flex>
              <v-flex tag="strong" xs5 text-xs-right mr-3 mb-2>Status:</v-flex>
              <v-flex>{{ selected.employee.status }}</v-flex>
            </v-layout>
          </v-card>
        </v-scroll-y-transition>
      </v-flex>
    </v-layout>
  </v-card>
</template>

<script>
import hazardsFromFile from './hazards.json'
const hazardsJson = hazardsFromFile;

const avatars = [
  '1.jpg',
  '2.jpg',
  '3.jpg',
  '4.jpg',
  '5.jpg',
  '6.jpg',
  '7.jpg',
  '8.jpg',
  '9.jpg',
  '10.jpg',
];

  //// const pause = ms => new Promise(resolve => setTimeout(resolve, ms))

  export default {
    data: () => ({
      active: [],
      avatar: null,
      open: [],
      hazards: []
    }),

    computed: {
      items () {
        return [
          {
            name: 'Hazards',
            children: this.hazards
          }
        ]
      },
      selected () {
        if (!this.active.length) return undefined

        const id = this.active[0]

        return this.hazards.find(user => user.id === id)
      }
    },

    watch: {
      selected: 'randomAvatar'
    },

    methods: {
      fetchhazards (item) {
/*
        return fetch('https://jsonplaceholder.typicode.com/hazards')
          .then(res => res.json())
          .then(json => (item.children.push(...json)))
          .catch(err => console.warn(err))
          */
         const hazardsEquipment = hazardsJson.filter(h => h.category == 'Echipamente');
         item.children.push(...hazardsEquipment)
      },
      randomAvatar () {
        const img = avatars[Math.floor(Math.random() * avatars.length)]
      const path = require('../../assets/'+ img );
      
      return path;
      }
    }
  }
</script>

